import { createStore } from "vuex";
import { getBlogs, getUser, getLabels } from "@/api/github";
import BlogData from "@/libs/blogClass";
import { ElMessage } from "element-plus";
import axios from "axios";
import Storage from "@/libs/storage";

let imgsResolve;

const store = createStore({
  state: {
    blogs: null,
    currentPage: 0, //当前页
    maxPage: 1, //最大页
    userData: null,
    markedDom: null,
    hljsDom: null,
    imgs: new Promise((re) => {
      imgsResolve = re;
    }),
    labels: null,
    //全局变量
    headerOffset: 20, //目录定位时，标签和header的距离
  },
  //可以看作是 store 的计算属性 以及 方法
  getters: {
    getBlogById: (state) => (number) => {
      return state.blogs ? state.blogs.get(number) : null;
    },
    getNextBlog: (state) => (currentBlog) => {
      //下一个博客
      if (!state.blogs) {
        return;
      }
      const blogs = [...state.blogs]; //从大到小的顺序，因此next是-1
      const index = blogs.findIndex((blog) => currentBlog === blog[1]);
      return blogs[index - 1]?.[1];
    },
    getLastBlog: (state) => (currentBlog) => {
      //上一个博客
      if (!state.blogs) {
        return;
      }
      const blogs = [...state.blogs];
      const index = blogs.findIndex((blog) => currentBlog === blog[1]);
      return blogs[index + 1]?.[1];
    },
  },
  //类似于声明了event.$on，使用store.commit调用, 只能同步进行
  mutations: {
    setBlogs(state, blogs) {
      const oldData = state.blogs ? [...state.blogs] : [];
      const newDate = oldData.concat(blogs);
      newDate.sort((x, y) => y[0] - x[0]);
      state.blogs = new Map(newDate);
    },
    updateCurrentPage(state, page) {
      state.currentPage = page;
    },
    updateMaxPage(state, page) {
      state.maxPage = page;
    },
  },
  // 可以进行异步操作后再调用mutations
  actions: {
    async getBlogsByPage(context, nextPage) {
      nextPage = nextPage ?? context.state.currentPage + 1;
      if (nextPage > context.state.maxPage) {
        ElMessage({
          message: "没有更多了",
          type: "info",
        });
        return;
      }
      const response = await getBlogs({ page: nextPage, per_page: 100 });
      if (response) {
        // console.log(response);
        if (nextPage === 1) {
          let re = /&page=(\d+)[&\w/_=]*>; rel="last"/;
          let maxPage = Number(re.exec(response.headers?.link)?.[1] || 1);
          context.commit("updateMaxPage", maxPage);
        }
        let blogs = [];
        response.data.map((d) => {
          const blogData = new BlogData(d);
          blogs.push([blogData.number, blogData]);
        });
        context.commit("setBlogs", blogs);
        context.commit("updateCurrentPage", nextPage);
      }
    },
    async checkUserData(context) {
      if (context.state.userData) {
        return context.state.userData;
      } else {
        const response = await getUser().catch((e) => {
          console.log(e);
        });
        if (response) {
          // console.log(response);
          context.state.userData = response.data;
          return context.state.userData;
        }
        return null;
      }
    },
    async setHljs(context, fileName = "a11y-light.css") {
      let hljsDom;
      if (context.state.hljsDom) {
        hljsDom = context.state.hljsDom;
      } else {
        hljsDom = window.document.createElement("style");
        window.document.head.appendChild(hljsDom);
        context.state.hljsDom = hljsDom;
      }
      axios
        .get(`${location.origin}${process.env.BASE_URL}static/hljs/${fileName}`)
        .then((cssFile) => {
          // console.log(cssFile)
          hljsDom.innerText = cssFile.data;
        });
    },
    async setMarkdown(context, fileName = "marked.css") {
      Storage.setThemeStyle(fileName);
      let markedDom;
      if (context.state.markedDom) {
        markedDom = context.state.markedDom;
      } else {
        markedDom = window.document.createElement("style");
        window.document.head.insertBefore(markedDom, context.state.hljsDom);
        context.state.markedDom = markedDom;
      }
      axios
        .get(
          `${location.origin}${process.env.BASE_URL}static/markedCss/${fileName}`
        )
        .then((cssFile) => {
          // console.log(cssFile)
          markedDom.innerText = cssFile.data;
        });
    },
    async getLabels(context) {
      if (context.state.labels) {
        return context.state.labels;
      } else {
        const response = await getLabels().catch((e) => {
          console.log(e);
        });
        if (response) {
          // console.log(response);
          context.state.labels = response.data.map((item) => {
            return {
              name: item.name,
              img: BlogData.getRandomImg(item.name),
            };
          });
          return context.state.labels;
        }
        return [];
      }
    },
    async getBlogsByLabel(context, labelName) {
      if (!context.state.blogs) {
        await store.dispatch("getBlogsByPage");
      }
      const blogs = [...context.state.blogs];
      const filterBlogs = blogs.filter((blog) => {
        const labels = blog[1].labels;
        return labels.some((label) => label.name === labelName);
      });
      return new Map(filterBlogs);
    },
    initImgsList() {
      axios
        .get(`${location.origin}${process.env.BASE_URL}static/img.txt`)
        .then((data) => {
          if (data?.data) {
            imgsResolve(data.data.match(/(http.+\..+)/g));
          } else {
            imgsResolve([]);
          }
        })
        .catch(() => {
          imgsResolve([]);
        });
    },
  },
});

//初始化
store.dispatch("initImgsList"); //获取img.txt
store.dispatch("getBlogsByPage");
store.dispatch("setHljs");
store.dispatch("setMarkdown", Storage.getThemeStyle());

export default store;
