import { createRouter, createWebHistory } from "vue-router";
import HomeView from "@/views/HomeView.vue";
import eventBus from "./libs/eventBus";

const routes = [
  {
    path: "/",
    name: "home",
    component: HomeView,
  },
  {
    path: "/archive",
    name: "archive",
    component: () => import("@/views/ArchiveView.vue"),
  },
  {
    path: "/archive/:number",
    name: "archive-content",
    component: () => import("@/views/ArchiveContent.vue"),
    props: true,
  },
  {
    path: "/pen",
    name: "pen",
    component: () => import("@/views/PenView.vue"),
  },
  {
    path: "/tools",
    name: "tools",
    component: () => import("@/views/ToolsView.vue"),
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

let scrollCache = {
  home: 0,
};

router.beforeEach((to, from, next) => {
  if (from.name === "home") {
    eventBus.emit("getScrollData", { obj: scrollCache, key: "home" });
  }
  next();
});

router.afterEach((to) => {
  if (to.name === "home") {
    eventBus.emit("scrollTo", { top: scrollCache.home });
    scrollCache.home = 0;
  } else {
    eventBus.emit("scrollTo");
  }
});

export default router;
