const USER = {
  DATA: "user_data",
};

const getLocalStorage = () => {
  const data = window.localStorage.getItem(USER.DATA) || '{"data":{}}';
  return JSON.parse(data)?.data || {};
};

const setLocalStorage = (data) => {
  window.localStorage.setItem(USER.DATA, JSON.stringify({ data }));
};

const get = (item) => {
  const obj = getLocalStorage();
  return obj[item];
};

const set = (item, data) => {
  const obj = getLocalStorage();
  obj[item] = data;
  setLocalStorage(obj);
};

export default {
  setThemeStyle(data) {
    set("ThemeStyle", data);
  },
  getThemeStyle() {
    return get("ThemeStyle");
  },
};
