<template>
  <div
    :class="[
      'blog-header',
      atTop ? 'at-top' : '',
      alwaysShow ? 'always-show' : '',
    ]"
  >
    <el-menu
      :default-active="$route.path"
      class="horizontal-menu"
      mode="horizontal"
      :ellipsis="false"
      router
    >
      <div class="menu-left">
        <BlogTitle class="blog-title" :at-top="atTop"></BlogTitle>
      </div>

      <div class="menu-content el-menu--horizontal">
        <el-menu-item class="menu-item faa-parent animated-hover" index="/">
          <i class="icon icon-home faa-horizontal"></i>
          <div>首页</div>
        </el-menu-item>
        <el-menu-item
          class="menu-item faa-parent animated-hover"
          :index="archive"
        >
          <i class="icon icon-label faa-shake"></i>
          <div>文章</div>
        </el-menu-item>
        <!-- <el-menu-item class="menu-item faa-parent animated-hover" index="/pen">
          <i class="icon icon-charge faa-tada"></i>
          <div>工具</div>
        </el-menu-item>
        <el-menu-item
          class="menu-item faa-parent animated-hover"
          index="/tools"
        >
          <i class="icon icon-impregnate faa-ring"></i>
          <div>收藏</div>
        </el-menu-item> -->
      </div>

      <div class="menu-right"></div>
    </el-menu>
  </div>
</template>

<script>
import BlogTitle from "./BlogTitle";
// import eventBus from "@/libs/eventBus";

export default {
  components: {
    BlogTitle,
  },
  props: {
    atTop: {
      typeof: Boolean,
      default: true,
    },
    alwaysShow: {
      typeof: Boolean,
      default: false,
    },
  },
  computed: {
    archive() {
      if (this.$route.name === "archive-content") {
        return this.$route.path;
      }
      return "/archive";
    },
  },
  data() {
    return {};
  },
};
</script>

<style lang="scss" scoped>
.blog-header.at-top:not(:hover):not(.always-show) {
  backdrop-filter: none;
  transition: none;
  .horizontal-menu {
    background-color: transparent;
    .menu-left {
      color: #000;
    }
    .menu-content {
      opacity: 0;
      transform: translate(10px);
    }
  }
}

.blog-header {
  user-select: none;
  position: fixed;
  top: 0;
  width: 100vw;
  height: 60px;
  z-index: 20;
  backdrop-filter: blur(4px);

  .horizontal-menu {
    position: relative;
    display: flex;
    justify-content: space-between;

    .menu-left {
      width: calc(50% - 500px);
      color: var(--el-fill-color-blank);
      @media screen and (max-width: 1440px) {
        .blog-title {
          display: none;
        }
      }
    }
    .menu-right {
      width: calc(50% - 500px);
    }
    @media screen and (max-width: 1000px) {
      .menu-left,
      .menu-right {
        display: none;
      }
    }
    .menu-content {
      opacity: 1;
      transform: translate(0);
      transition: all 0.4s ease-in-out;

      width: 1000px;
      display: flex;
      justify-content: flex-start;
    }
    .menu-item {
      width: 100px;
      // font-family: "Poppins", sans-serif;
      .icon {
        margin-right: 4px;
      }
    }
  }
}
</style>