//一言api
import request from "@/libs/request";

export const getAword = (
  { type = "i", minLength = 14, maxLength = 16 } = {
    type: "i",
    minLength: 14,
    maxLength: 16,
  }
) => {
  return request.get(
    `https://v1.hitokoto.cn/?c=${type}&min_length=${minLength}&max_length=${maxLength}`
  );
};
