<template>
  <div class="color-card">
    <div class="wrap" :class="left ? 'left' : 'right'">
      <div class="img-wrap">
        <el-image
          class="img"
          :src="img || loadingImg"
          fit="cover"
          loading="lazy"
        >
          <template #placeholder>
            <img class="img" :src="loadingImg" />
          </template>
        </el-image>
      </div>

      <div class="text-wrap">
        <div class="title" :title="title">{{ title }}</div>
        <div class="time">
          <i class="icon-schedule"></i>
          {{ time }}
        </div>

        <div class="intro">{{ intro }}</div>
        <div class="labels">
          <div
            class="label"
            v-for="label in labels"
            :key="label.id"
            :style="`background:#${label.color}; border-color:#${label.color};`"
          >
            {{ label.name }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    loadingImg: {
      type: String,
      default: "",
    },
    title: {
      type: String,
      default: "标题",
    },

    time: {
      type: String,
      default: "时间",
    },

    intro: {
      type: String,
      default: "简介",
    },
    left: {
      type: Boolean,
      default: true,
    },
    labels: {
      type: Array,
      default: () => {
        return [];
      },
    },
    img: {
      type: String,
      default: "",
    },
  },
};
</script>

<style lang="scss" scoped>
.color-card {
  font-family: "Voces", "Open Sans", arial, sans-serif;
  height: 300px;
  margin-bottom: 24px;

  box-shadow: 0 1px 20px -6px rgb(0 0 0 / 50%);
  border-radius: 10px;
  overflow: hidden;

  .wrap {
    display: flex;
    height: 100%;
    .img-wrap {
      position: relative;
      overflow: hidden;
      height: 100%;
      width: calc(100% - 500px);

      .img {
        transition: transform 0.7s ease;
        height: 300px;
        width: 500px;
        object-fit: cover;
      }
    }
    .text-wrap {
      position: relative;
      //   background: #fff;
      width: 500px;
      .title {
        margin-top: 24px;
        font-size: 24px;
        color: #333;
        overflow: hidden;
        height: 30px;
        line-height: 30px;
        text-overflow: ellipsis;
        width: 100%;
        white-space: nowrap;
      }
      .time {
        margin-top: 4px;
        font-size: 18px;
        color: #888;
        height: 20px;
        line-height: 20px;
      }
      .intro {
        margin-top: 40px;
        font-size: 16px;
        color: #424242;
        height: 60px;
        line-height: 20px;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
        overflow-wrap: break-word;
      }

      .labels {
        position: absolute;
        bottom: 24px;
        font-size: 14px;
        cursor: default;
        pointer-events: none;

        .label {
          display: inline-block;
          background: transparent;
          color: #fff;
          border-radius: 3px 0 0 3px;
          line-height: 26px;
          padding: 0 10px 0 23px;
          position: relative;
          margin-right: 20px;
        }
        .label::before {
          content: "";
          position: absolute;
          background: #fff;
          border-radius: 10px;
          box-shadow: inset 0 1px rgba(0, 0, 0, 0.25);
          height: 6px;
          left: 10px;
          width: 6px;
          top: 10px;
        }
        .label::after {
          content: "";
          position: absolute;
          border-bottom: 13px solid transparent;
          border-left: 11px solid transparent;
          border-top: 13px solid transparent;
          right: -10px;
          top: 0;
          border-left-color: inherit;
        }
      }
    }
  }
}

@media (min-width: 500px) {
  .color-card {
    .wrap {
      &.left {
        flex-direction: row;
        .text-wrap {
          padding: 0 24px 0 12px;

          background-image: -webkit-linear-gradient(
            340deg,
            #fff,
            #fff 10%,
            transparent
          );
          &::before {
            content: " ";
            position: absolute;
            border-left: 51px solid transparent;
            border-bottom: 300px solid #fff;
            left: -50px;
          }
        }
      }
      &.right {
        flex-direction: row-reverse;
        .text-wrap {
          padding: 0 12px 0 24px;
          background-image: -webkit-linear-gradient(
            340deg,
            transparent,
            #fff 90%,
            #fff
          );
          &::before {
            content: " ";
            position: absolute;
            border-left: 51px solid #fff;
            border-bottom: 300px solid transparent;
            right: -50px;
          }
        }
      }
    }
  }
}
@media (max-width: 500px) {
  .color-card {
    .wrap {
      .img-wrap {
        display: none;
      }
      .text-wrap {
        width: 100%;
        padding: 0 8px;
        background: #fff;
      }
    }
  }
}
.color-card:hover {
  cursor: pointer;
  box-shadow: 0 5px 10px 5px rgb(110 110 110 / 40%);
  .wrap.left {
    .img-wrap {
      .img {
        transform: scale(1.3) rotate(5deg);
      }
    }
  }
  .wrap.right {
    .img-wrap {
      .img {
        transform: scale(1.3) rotate(-5deg);
      }
    }
  }
}
</style>