<template>
  <el-scrollbar
    ref="scrollbar"
    height="100vh"
    always
    @scroll="assertScroll"
    :wrap-style="`scroll-padding-top: ${60 + $store.state.headerOffset}px;`"
  >
    <BlogHeader :at-top="atTop" :always-show="!showHomeImage" />
    <HomeImage
      ref="homeImage"
      @scroll-to-content="scrollToContent"
      v-if="showHomeImage"
    />
    <div class="router-view-content">
      <router-view v-slot="{ Component }">
        <keep-alive :include="['HomeView']">
          <component :is="Component" />
        </keep-alive>
      </router-view>
    </div>
    <footer class="footer">
      <p>{{ `Copyright © zxz's blog ${years}` }}</p>
      <p>
        {{ "Powered by " }}
        <a href="https://github.com/pma934/pma934.github.io"
          ><img
            src="https://img.shields.io/badge/GitHub-100000?style=for-the-badge&logo=github&logoColor=white"
        /></a>
      </p>
    </footer>
    <BackToTop
      :scroll-data="scrollData"
      @to-Top="scrollTo({ behavior: 'smooth' })"
    />
    <ThemeSwitch />
  </el-scrollbar>
</template>

<script>
import BlogHeader from "@/components/BlogHeader";
import HomeImage from "@/components/HomeImage";
import BackToTop from "@/components/BackToTop";
import ThemeSwitch from "@/components/ThemeSwitch";
import eventBus from "@/libs/eventBus.js";
// import { debounce } from "@/libs/utils";

export default {
  components: {
    BlogHeader,
    HomeImage,
    BackToTop,
    ThemeSwitch,
  },
  data() {
    return {
      atTop: true,
      scrollData: {
        clientHeight: 0,
        scrollTop: 0,
        scrollHeight: 0,
      },
      years: new Date().getFullYear(),
    };
  },
  // provide() {
  //   return {
  //     getScrollData:  () => {
  //       console.log('getScrollData')
  //       return this.scrollData;
  //     },
  //   };
  // },
  computed: {
    showHomeImage() {
      return ["home", "tools"].includes(this.$route.name);
    },
  },
  mounted() {
    this.assertScroll();
    eventBus.on("scrollTo", this.scrollTo);
    eventBus.on("getScrollData", ({ obj, key }) => {
      if (obj) {
        const { scrollTop } = this.$refs.scrollbar.wrapRef;
        obj[key] = scrollTop;
      }
    });
  },
  unmounted() {
    eventBus.off("scrollTo");
    eventBus.off("getScrollData");
  },
  methods: {
    scrollTo(
      { top = 0, left = 0, behavior = "auto" } = {
        top: 0,
        left: 0,
        behavior: "auto",
      }
    ) {
      this.$nextTick(() => {
        this.$refs.scrollbar.scrollTo({
          top,
          left,
          behavior,
        });
      });
    },
    scrollToContent() {
      const offsetHeight = this.$refs.homeImage.$el.offsetHeight;
      this.$refs.scrollbar.scrollTo({
        top: offsetHeight - 60, //注意这里的60现在是固定的header高度，不知道后期会不会改成可配置
        behavior: "smooth",
      });
    },
    // 装饰器语法，防抖
    // @debounce(1000 / 60)
    assertScroll() {
      const { clientHeight, scrollTop, scrollHeight } =
        this.$refs.scrollbar.wrapRef;

      this.atTop = false;
      this.scrollData = {
        clientHeight, //窗口高
        scrollTop, //滚动距离
        scrollHeight, //总滚动高度
      };

      // console.log(clientHeight, scrollTop, scrollHeight);

      eventBus.emit("scroll", scrollTop);
      if (scrollTop === 0) {
        // console.log("到顶了");
        eventBus.emit("atTop");
        this.atTop = true;
      } else {
        if (clientHeight + scrollTop === scrollHeight) {
          // console.log("到底了");
          eventBus.emit("atBottom");
        }
      }
    },
  },
};
</script>

<style lang="scss">
#app {
  height: 100vh;
  background-image: url(@/assets/img/sakura.png);
  // background-size: cover;
  background-position: center;
  .router-view-content {
    min-height: calc(100vh - 120px);
    max-width: 1000px;
    // min-width: 600px;
    margin-left: auto;
    margin-right: auto;
    background-color: var(--el-bg-color);
  }
  .footer {
    padding: 20px;
    height: 120px;
    max-width: 1000px;
    // min-width: 600px;
    margin-left: auto;
    margin-right: auto;
    background-color: var(--el-bg-color);
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    p {
      line-height: 24px;
      font-size: 16px;
      height: 24px;
      img {
        height: 20px;
        margin-left: 4px;
        vertical-align: sub;
      }
    }
  }
}
</style>
