<template>
  <div
    class="back-to-top"
    :class="show ? 'show' : 'hidden'"
    @click="$emit('toTop')"
  >
    <!-- <i class="icon-camp"></i> -->
    <svg viewBox="0 0 200 200" height="100%" width="100%">
      <circle
        cx="100"
        cy="100"
        r="91"
        fill="white"
        stroke-width="20"
        stroke="white"
        :style="`stroke-dasharray: 600, 600; stroke-dashoffset: ${
          600 * progress
        }`"
      ></circle>
    </svg>
  </div>
</template>

<script>
export default {
  props: {
    scrollData: {
      type: Object,
      required: true,
    },
  },
  computed: {
    show() {
      return (
        this.scrollData.scrollTop &&
        this.scrollData.scrollTop > this.scrollData.clientHeight / 2
      );
    },
    progress() {
      return (
        (this.scrollData.scrollTop + this.scrollData.clientHeight) /
        this.scrollData.scrollHeight
      );
    },
  },
};
</script>

<style lang="scss" scoped>
.back-to-top {
  --el-backtop-bg-color: var(--el-bg-color-overlay);
  --el-backtop-text-color: var(--el-color-primary);
  --el-backtop-hover-bg-color: var(--el-border-color-extra-light);
  position: fixed;
  background-color: var(--el-backtop-bg-color);
  width: 40px;
  height: 40px;
  border-radius: 50%;
  color: var(--el-backtop-text-color);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  box-shadow: var(--el-box-shadow-lighter);
  cursor: pointer;
  z-index: 5;

  right: 40px;
  bottom: 40px;
  opacity: 1;
  transition: all 0.8s cubic-bezier(0.4, 0, 0.2, 1);

  //   background: conic-gradient(
  //     hsl(360, 100%, 50%),
  //     hsl(315, 100%, 50%),
  //     hsl(270, 100%, 50%),
  //     hsl(225, 100%, 50%),
  //     hsl(180, 100%, 50%),
  //     hsl(135, 100%, 50%),
  //     hsl(90, 100%, 50%),
  //     hsl(45, 100%, 50%),
  //     hsl(0, 100%, 50%)
  //   );
  background-image: url("@/assets/捕获.PNG");
  background-position: center;

  font-size: 24px;
  &:hover {
    color: var(--el-color-warning-light-3);
  }

  &.hidden {
    bottom: -40px;
    opacity: 0;
  }

  //   i {
  //     position: absolute;
  //   }
  &::before {
    content: "\e917";
    font-family: "icomoon" !important;
    position: absolute;
    z-index: 10;
  }

  svg {
    transform: rotateZ(90deg) rotateY(180deg);
  }
}
</style>