import { marked } from "marked";
import hljs from "highlight.js";
import store from "@/store";
import {randomImgApi} from '@/libs/const'

let imgs;

marked.setOptions({
  renderer: new marked.Renderer(),
  highlight: function (code, lang) {
    lang = lang.split(".").slice(-1)[0]; //截取末尾
    const language = hljs.getLanguage(lang) ? lang : "js"; //没有匹配上的的就按js高亮处理
    const res = hljs.highlight(code, { language }).value;
    return res;
  },
  langPrefix: "hljs language-", // highlight.js css expects a top-level 'hljs' class.
  pedantic: false,
  gfm: true,
  breaks: false,
  sanitize: false,
  smartypants: false,
  xhtml: false,
});

const getDate = (create_date) => {
  const cD = new Date(create_date);
  const nD = new Date();
  const cDy = cD.getFullYear();
  const cDm = cD.getMonth();
  const cDd = cD.getDate();
  const nDy = nD.getFullYear();
  const nDm = nD.getMonth();
  const nDd = nD.getDate();
  if (cDy === nDy) {
    if (cDm === nDm) {
      if (cDd === nDd) {
        return "today";
      } else {
        return nDd - cDd === 1 ? `yesterday` : `${nDd - cDd} days ago`;
      }
    } else {
      return nDm - cDm === 1 ? `a month ago` : `${nDm - cDm} months ago`;
    }
  } else {
    return nDy - cDy === 1 ? `a year ago` : `${nDy - cDy} years ago`;
  }
};

// 注释用语法
// <!--{ "summary":"对事件发生顺序、事件绑定、事件委托的一些个人理解", "img":"https://img-blog.csdnimg.cn/20190117102631744.png" }-->
const getIntro = (str) => {
  const json = str.match(/^<!--([\s\S]*?)-->/)?.[1]; //[\s\S]匹配包括换行的任意字符
  let obj = {};
  if (json) {
    try {
      obj = JSON.parse(json);
    } catch (e) {
      //
    }
  }
  if (obj?.summary) {
    return obj.summary;
  }
  return str.replaceAll(/<\/[\s\S]*?>/g, "\n").replaceAll(/<[\s\S]*?>/g, "");
};

const initImg = (str, that) => {
  const json = str.match(/^<!--([\s\S]*?)-->/)?.[1]; //[\s\S]匹配包括换行的任意字符
  let obj = {};
  if (json) {
    try {
      obj = JSON.parse(json);
    } catch (e) {
      //
    }
  }
  if (obj?.img) {
    return obj.img;
  }
  that.getImgByList();
  return "";
};

export default class BlogData {
  constructor(data) {
    this.url = data.html_url;
    this.number = data.number;
    this.title = data.title;
    this.blogHtml = data.body ? marked.parse(data.body) : "";
    this.intro = getIntro(this.blogHtml);
    this.time = getDate(data.created_at);
    this.labels = data.labels;
    this.img = initImg(this.blogHtml, this);
  }
  async getImgByList() { //从img.txt获取图片地址
    const data = await store.state.imgs;
    if(!imgs){
      imgs = data;
    }
    if(imgs.length){
      const random = Number.parseInt(imgs.length*Math.random());
      this.img = imgs.splice(random,1)[0];
    } else {
      this.img = BlogData.getRandomImg(this.number);
    }
    store.commit('setBlogs',[]) //img更新后，通知store刷新blogs数据
  }
  static getRandomImg(flag){
    const random = Number.parseInt(randomImgApi.length*Math.random());
    const url = randomImgApi[random];
    return `${url}?r=${flag}`
  }
}
