<template>
  <div class="theme-switch" :class="expansionClass" v-if="$route.name==='archive-content'" @click.stop="closeContent">
    <div class="open" v-show="!expansion.showContent" @click.stop="openContent">
      <i class="icon-navigate"></i>
    </div>
    <div
      class="content"
      :class="expansion.showItem ? 'show-item' : ''"
      v-show="expansion.showContent"
    >
      <template v-for="(item, index) in markedFileName" :key="item">
        <div
          class="theme-item"
          :class="{ active: item === value }"
          :style="ThemeStyle[item]"
          @click.stop="changeTheme(index)"
        ></div>
      </template>
    </div>
  </div>
</template>

<script>
import { markedFileName, ThemeStyle } from "@/libs/const";
import Storage from "@/libs/storage";

export default {
  data() {
    return {
      value: Storage.getThemeStyle(),
      markedFileName: markedFileName,
      ThemeStyle: ThemeStyle,
      expansion: {
        x: false,
        y: false,
        showContent: false,
        showItem: false,
      },
    };
  },
  computed: {
    expansionClass() {
      return `${this.expansion.x ? "expansion-x" : ""} ${
        this.expansion.y ? "expansion-y" : ""
      }`;
    },
  },
  methods: {
    changeTheme(item) {
      this.value = this.markedFileName[item];
      this.$store.dispatch("setMarkdown", this.value);
      this.closeContent();
    },
    async openContent() {
      if (this.expansion.showContent) {
        return;
      }
      this.expansion.showContent = true;
      await new Promise((re) => {
        setTimeout(() => {
          re();
        }, 0);
      });
      this.expansion.x = true;
      await new Promise((re) => {
        setTimeout(() => {
          re();
        }, 300);
      });
      this.expansion.y = true;
      await new Promise((re) => {
        setTimeout(() => {
          re();
        }, 300);
      });
      this.expansion.showItem = true;
    },
    async closeContent() {
      this.expansion.showItem = false;
      await new Promise((re) => {
        setTimeout(() => {
          re();
        }, 200);
      });
      this.expansion.y = false;
      await new Promise((re) => {
        setTimeout(() => {
          re();
        }, 0);
      });
      this.expansion.x = false;
      await new Promise((re) => {
        setTimeout(() => {
          re();
        }, 300);
      });
      this.expansion.showContent = false;
    },
  },
};
</script>

<style lang="scss" scoped>
.theme-switch {
  --el-backtop-bg-color: var(--el-bg-color-overlay);
  --el-backtop-text-color: var(--el-color-primary);
  --el-backtop-hover-bg-color: var(--el-border-color-extra-light);
  position: fixed;
  background-color: var(--el-backtop-bg-color);
  width: 40px;
  height: 40px;
  border-radius: 20px;
  color: var(--el-backtop-text-color);
  // display: flex;
  // align-items: center;
  // justify-content: center;
  // font-size: 20px;
  box-shadow: var(--el-box-shadow-lighter);
  z-index: 5;

  right: 40px;
  bottom: 100px;

  font-size: 24px;
  &:hover {
    color: var(--el-color-warning-light-3);
  }
  cursor: pointer;

  .content,
  .open {
    position: absolute;
    right: 0;
    bottom: 0;
    height: 40px;
    width: 40px;
    background: inherit;
    border-radius: 20px;
    transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);

    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-evenly;
    .theme-item {
      height: 40px;
      width: 40px;
      border-radius: 20px;
      margin: 2px;
      border: 2px solid #d3d3d3;
      box-shadow: var(--el-box-shadow-lighter);

      opacity: 0;
      transition: opacity 0.3s ease-in-out;
      &.active {
        border: 2px solid var(--el-color-primary-dark-2);
        transform: scale(1.05);
        box-shadow: var(--el-box-shadow-dark);
      }
      &:hover{
        transform: scale(1.05);
      }
    }
  }
  .content.show-item {
    .theme-item {
      opacity: 1;
    }
  }
  .open {
    border: 2px solid #d3d3d3;
  }
  &.expansion-x {
    .content {
      width: 200px;
      right: 20px;
      border-bottom-right-radius: 0;
      border-top-right-radius: 0;
    }
  }
  &.expansion-y {
    .content {
      border-top-right-radius: 20px;
      height: 200px;
    }
  }
  .icon-navigate {
    animation: rotate 4s linear infinite;
  }
  @keyframes rotate {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
}
</style>