<template>
  <div class="home">
    <!-- <div class="small-top">
      <div class="small-top-title">小玩具</div>
      <div class="small-top-card">
        <div :key="i" v-for="i in 3">
          <el-image :src="imgUrl" fit="cover" loading="lazy">{{ i }}</el-image>
        </div>
      </div>
    </div> -->
    <div class="content-box" v-if="blogs">
      <div class="content-box-title">阅读</div>
      <div class="content-box-wrap">
        <template
          v-for="([number, blog], index) in [...blogs].slice(0, showBlogNumber)"
          :key="number"
        >
          <ColorCard
            :left="!!(index % 2)"
            :loading-img="loadingImg"
            v-bind="{
              title: blog.title,
              time: blog.time,
              intro: blog.intro,
              labels: blog.labels,
              img: blog.img,
            }"
            @click="$router.push('/archive/' + number)"
          />
        </template>
        <span v-show="showLoading">加载中</span>
      </div>
    </div>
  </div>
</template>

<script>
import eventBus from "@/libs/eventBus";
import ColorCard from "@/components/ColorCard.vue";

export default {
  name: "HomeView",
  components: { ColorCard },
  data() {
    return {
      showBlogNumber: 5, //当前显示的blog数量
      imgUrl: new URL(`@/assets/img/珂朵莉.jpg`, import.meta.url).href,
      loadingImg: new URL(`@/assets/img/loading.gif`, import.meta.url).href,
      showLoading: false,
    };
  },
  // inject: ["getScrollData"], //from App.vue
  computed: {
    blogs() {
      return this.$store.state.blogs;
    },
  },
  activated() {
    //绑定滚动到底加载blog
    eventBus.on("atBottom", () => {
      // console.log(this.showBlogNumber);
      if (this.showBlogNumber < this.blogs.size) {
        this.showBlogNumber += 5;
      } else {
        this.showLoading = true;
        this.$store.dispatch("getBlogsByPage").finally(() => {
          if (this.showBlogNumber < this.blogs.size) {
            this.showBlogNumber += 5;
          }
          this.showLoading = false;
        });
      }
    });
  },
  deactivated() {
    eventBus.off("atBottom");
  }
};
</script>

<style lang="scss" scoped>
.home {
  padding: 0 16px;
  transform: translate3d(0, 0, 0); //减少卡顿
  .small-top {
    padding-top: 40px;
    .small-top-title {
      color: var(--el-overlay-color-light);
      font-size: 16px;
      font-weight: 400;
      padding-bottom: 5px;
      margin-bottom: 30px;
      border-bottom: 1px dashed var(--el-overlay-color-lighter);
    }
    .small-top-card {
      display: flex;
      justify-content: space-between;
      div {
        position: relative;
        width: calc((100% - 9px) / 3);
        padding-bottom: calc((100% - 9px) / 3 * 2 / 3);
        height: 0;
        div {
          width: 100%;
          height: 100%;
          position: absolute;
          top: 0;
          left: 0;
          padding: 0;
          border-radius: 4px;
          box-shadow: 0 1px 20px -6px rgb(0 0 0 / 50%);
        }
      }
    }
  }
  .content-box {
    padding-top: 40px;
    .content-box-title {
      color: var(--el-overlay-color-light);
      font-size: 16px;
      font-weight: 400;
      padding-bottom: 5px;
      margin-bottom: 30px;
      border-bottom: 1px dashed var(--el-overlay-color-lighter);
    }
    .content-box-wrap {
      padding-bottom: 4px;
    }
  }
}
</style>
