import request from "@/libs/request";

//获取文件标签
export const getLabels = (owner = "pma934", repo = "pma934.github.io") => {
  return request.get(`https://api.github.com/repos/${owner}/${repo}/labels`);
};

//获取文章列表
export const getBlogs = ({
  page,
  per_page = 100,
  // owner = "vuejs",
  // repo = "vue",
  owner = "pma934",
  repo = "pma934.github.io",
  labels
}) => {
  return request.get(
    `https://api.github.com/repos/${owner}/${repo}/issues?per_page=${per_page}&page=${page}&creator=${owner}${labels?'&labels='+labels:''}`,{hasErrorHandle:false}
  );
};

//获取指定文章
export const getBlog = ({
  number,
  // owner = "vuejs",
  // repo = "vue",
  owner = "pma934",
  repo = "pma934.github.io",
}) => {
  return request.get(
    `https://api.github.com/repos/${owner}/${repo}/issues/${number}`
  );
};

//获取用户信息
export const getUser = (owner="pma934")=>{
  return request.get(
    `https://api.github.com/users/${owner}`,{hasErrorHandle:false}
  );
}